.marketingContainer {
  background-color: #535772;
  min-height: 400px;
  height: max-content;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 50px;
  padding-bottom: 50px;
  align-items: center;
}
.imageContainer {
  width: 80%;
  align-items: center;
}

.imageContainer,
.marketingContainer,
.buttonContainer {
  display: flex;
  justify-content: center;
}
.buttonContainer button {
  border-radius: 15px;
  line-height: 40px;
  border: 1px solid #707070;
  filter: drop-shadow(0px 5px 15px rgba(0, 0, 0, 0.1607843137254902));
  font-family: Poppins, sans-serif;
  font-weight: 500;
}
.buttonContainer button:nth-child(1) {
  height: 82px;
  width: 253px;
}

.buttonContainer button:nth-child(2) {
  height: 82px;
  width: 344px;
}
.image {
  max-height: 118px;
}

.marketingContainer .imageContainer .divider {
  background-color: #fff;
  width: 2px;
  border-radius: 12px;
  margin-left: 30px;
  margin-right: 30px;
  height: 80px;
  align-self: center;
}
.marketingContainer .imageContainer .cellarTechDesc {
  padding-left: 0;
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  color: #0099ff;
}
.marketingContainer h1 .marketingContainer h4 {
  font-family: Poppins, sans-serif;
}
.marketingContainer h1 {
  font-size: 55px;
  font-weight: 600;
  line-height: 83px;
  margin-bottom: 22px;
  font-size: 55px;
}

.marketingContainer h4 {
  font-size: 28px;
  font-weight: 400;
  line-height: 42px;
}

.marketingContainer h4,
.marketingContainer h1 {
  text-align: center;
  color: #fff;
  font-family: "Poppins", sans-serif;
}

.buttonContainer {
  margin-top: 40px;
  padding-left: 20px;
  padding-right: 20px;
}

@media only screen and (max-width: 767px) {
  .imageContainer,
  .marketingContainer,
  .buttonContainer {
    flex-direction: column;
    align-items: center;
  }
  .marketingContainer {
    min-height: 500px;
  }
  .marketingContainer .imageContainer .divider {
    display: none;
  }
  .image {
    max-height: 80px;
  }
  .marketingContainer h1 {
    font-size: 35px;
    line-height: 52px;
    margin-bottom: 18px;
  }
  .marketingContainer h4 {
    font-size: 22px;
    line-height: 30px;
  }

  .buttonContainer button:nth-child(1) {
    margin-bottom: 20px;
    margin-right: 0;
  }
  .buttonContainer button:nth-child(1) {
    height: 60px;
    width: 253px;
  }

  .buttonContainer button:nth-child(2) {
    height: 67px;
    width: 310px;
  }
}
@media only screen and (max-width: 400px) {
  .marketingContainer h1 {
    font-size: 30px;
    line-height: 48px;
  }
  .marketingContainer h4 {
    font-size: 20px;
  }
  .buttonContainer button:nth-child(1) {
    height: 60px;
    width: 253px;
  }

  .buttonContainer button:nth-child(2) {
    height: 67px;
    width: 310px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1330px) {
  .marketingContainer h1 {
    font-size: 42px;
    line-height: 62px;
    margin-bottom: 20px;
  }
  .marketingContainer h4 {
    font-size: 24px;
    line-height: 34px;
  }
  .buttonContainer button:nth-child(1) {
    height: 60px;
    width: 253px;
  }

  .buttonContainer button:nth-child(2) {
    height: 67px;
    width: 310px;
  }
}
