.adressFooterList h5,
.footerList h5 {
  position: relative;
  margin-bottom: 25px;
  padding-bottom: 10px;
  font-size: 26px;
  line-height: 35px;
  font-weight: 500;
  color: "#fff";
}
.footerList a {
  text-decoration: none;
  color: #fff;
}
.adressFooterList .address,
.adressFooterList .email {
  margin-bottom: 16px;
}
.adressFooterList .address,
.adressFooterList .email,
.adressFooterList .phone {
  display: flex;
}
.adressFooterList,
.footerList {
  display: flex;
  justify-content: center;
}
.footerList ul li,
.adressFooterList ul a,
.adressFooterList .address,
.adressFooterList .email,
.adressFooterList .phone {
  margin-bottom: 12px;
  left: 0;
  font-size: 23px;
  position: relative;
  font-weight: 400;
  line-height: 35px;
  transition: all 0.3s ease;
}

.cellarTechLogo {
  height: 100px;
  padding: 30px;
}
.cellarTechDesc {
  padding-left: 30px;
  margin-top: 10px;
  color: #2498fb;
  width: 80%;
  font-size: 24px;
  /* letter-spacing: .24px; */
  font-family: Montserrat, sans-serif;

  font-weight: normal;
  line-height: 29px;
}
.brandingContainer {
  position: relative;
}
.brandingContainer:after {
  content: "";
  position: absolute;
  height: 70%;
  top: 50%;
  right: 37px;
  transform: translateY(-50%);
  float: left;
  border-right: 3px solid #fff;
  padding-right: 20px;
}
@media only screen and (max-width: 767px) {
  .adressFooterList h5,
  .footerList h5 {
    font-size: 23px;
  }
  .adressFooterList,
  .footerList {
    justify-content: flex-start;
    padding-left: 30px;
    margin-bottom: 30px;
  }
  .cellarTechLogo {
    padding-bottom: 10px;
  }
  .cellarTechDesc {
    font-size: 18px;
  }
  .brandingContainer:after {
    display: none;
  }
  .footerList ul li,
  .adressFooterList ul a,
  .adressFooterList .address,
  .adressFooterList .email,
  .adressFooterList .phone {
    font-size: 20px;
  }
}
@media only screen and (max-width: 1280px) {
  .brandingContainer:after {
    display: none;
  }
}

@media only screen and (min-width: 600px) and (max-width: 960px) {
  .adressFooterList,
  .footerList {
    justify-content: flex-start;
    padding-left: 30px;
    margin-bottom: 30px;
  }
  .brandingContainer:after {
    display: none;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1330px) {
  .adressFooterList h5,
  .footerList h5 {
    font-size: 24px;
  }
  .footerList ul li,
  .adressFooterList ul a,
  .adressFooterList .address,
  .adressFooterList .email,
  .adressFooterList .phone {
    font-size: 20px;
  }
  .cellarTechLogo {
    padding-bottom: 15px;
  }
  .brandingContainer:after {
    display: none;
  }
}

.footerList ul,
.adressFooterList ul {
  list-style-type: none;
  text-decoration: none;
  text-indent: 0;
  cursor: pointer;
  padding: 0;
}

/* .footerList ul li,
.adressFooterList ul li {
  padding-bottom: 10px;
} */

.footerList ul li:before,
.adressFooterList ul a:before {
  font-weight: bold;
  padding-right: 10px;
}

/*Footer SECTION ICONS */
.footerList ul li::before {
  content: "\f0da";
  font-family: "FontAwesome";
}
.footerList ul li:hover {
  left: 10px;
  transition: all 0.3s ease-in;
  color: "#fff";
}
.adressFooterList a {
  text-decoration: none;
  color: #fff;
}

/*Address SECTION ICONS */
.address .MuiSvgIcon-root {
  height: 34px;
  width: 24.25px;
  margin-right: 8px;
}
.email .MuiSvgIcon-root {
  height: 27px;
  width: 37px;
}
.phone .MuiSvgIcon-root {
  height: 33.25;
  width: 33.25px;
}
