.landing-page-content-container {
  max-width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

#home {
  padding-top: clamp(50px, 10%, 150px);
  padding-bottom: 50px;
}

.landing-page-content-container h1 {
  letter-spacing: 0px;
  font-size: 70px;
  line-height: 106px;
  font-weight: 700;

  font-family: "Poppins", sans-serif;
  color: #535772;
  opacity: 1;
}
.buttonContainer {
  display: flex;
  justify-content: flex-start;
  flex-shrink: 0;
}
.specialButton {
  font-family: Poppins, sans-serif;
}
.specialButton,
.specialButton:hover {
  box-shadow: none !important;
  background-color: transparent;
}

.specialButton .MuiButton-label {
  justify-content: flex-start;
}
.specialButton .MuiButton-label:after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 1em;
  height: 5px;
  width: 83px;
  border-radius: 15px;

  background-color: #2498fb;
}

@media only screen and (max-width: 812px) {
  .landing-page-content-container h1 {
    font-size: 35px;
    line-height: 50px;
    text-align: center;
    margin-bottom: 0;
  }
  .landing-page-content-container {
    width: 100%;
    max-width: 100%;
  }
  .landing-page-content-container {
    margin-bottom: 30px;
  }
  .specialButton {
    margin-right: 0;
  }
}

@media only screen and (max-width: 960px) {
  .landingImage {
    max-height: 400px;
  }
  .gridContainer {
    display: flex;
    justify-content: center;
  }
}

@media only screen and (min-width: 812px) and (max-width: 1330px) {
  .landing-page-content-container h1 {
    font-size: 58px;
    line-height: 80px;
  }
}
