.serviceContainer,
.serviceCardContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  transition: all 0.6s ease-in;
  flex-wrap: wrap-reverse;
  /* margin-top: 20px; */
}

.serviceContainer {
  background-image: url("../images/servicesBack.png");
  /* background-size: cover;
background-color: red; */
}

.serviceCardContainer:nth-child(1) .card:nth-child(2) {
  margin-top: 50px;
}

.serviceContainer {
  min-width: 100%;
  padding-top: 40px;
}
.serviceListContainer,
.serviceDetailContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.serviceCardContent ul li {
  list-style-type: none;
  text-decoration: none;
  font-family: Montserrat;
  font-weight: semi-bold;
  text-indent: 0;
  font-size: 22px;
  padding: 0;
}

.card {
  padding: 0 1.7rem;
  min-height: 300px;
  min-width: 400px;
  max-width: 400px;
  align-items: flex-end;
  display: flex;

  margin-bottom: 50px;
}
.wrapper {
  position: relative;
  height: 100%;
  width: 100%;

  min-height: 220px;
  max-width: 450px;
}
.cardImage {
  position: absolute;
  top: -80px;
  width: 100%;
  display: flex;
  justify-content: center;
  transition: transform 0.3s;
}
.cardImage img {
  max-height: 110px;
  filter: drop-shadow(0px 10px 26px #00000029);
}

.cardContent {
  min-height: 150px;
  padding: 20px;
  box-shadow: 0px 4px 35px #0000001a;
  border-radius: 18px;
}
.cardHeader {
  text-align: center;
  font-family: Montserrat, sans-serif;
  font-weight: 600;
  line-height: 34px;

  font-weight: 28px;
}
.cardContent ul li {
  list-style-type: none;
  display: flex;
  align-items: flex-start;
  padding: 0;
  font-family: Poppins, sans-serif;
  font-weight: 400;
  color: #808080;
  line-height: 30px;
}

.serviceTitle {
  position: relative;
  width: min-content;
  color: #2498fb;
  font-weight: 400;
  line-height: 43px;
  font-size: 35px;
  font-family: Montserrat, sans-serif;
}

.serviceTitle:before {
  content: "";
  position: absolute;
  top: -5px;
  left: 0;
  box-shadow: 0px 5px 15px #00000029;
  border-radius: 15px;

  width: 70%;
  background-color: #2498fb;
  height: 3.3px;
  border-radius: 12px;
}

.serviceDetails {
  padding: 10px 25px;
  padding-bottom: 30px;
  background: #fff;
  border: 2px dashed #000;
  width: 70%;
  border-radius: 18px;

  color: #808080;
}
.serviceDetails h3 {
  font-size: 30px;
}

.serviceDetailContainer h1 {
  font-size: 40px;
  margin-top: 0;
  line-height: 69px;
  font-weight: 600;
}

.serviceDetailContainer .serviceTitle {
  font-size: 28px !important;
  line-height: 43px;
  font-weight: 400;
  font-family: Montserrat, sans-serif;
  mix-blend-mode: normal;
}
.serviceDetails ul {
  list-style-type: none;
  text-decoration: none;
  text-indent: 0;
  padding: 0;
  margin-bottom: 33px;
  color: #2498fb;
}

.serviceDetails ul li {
  font-family: Montserrat, sans-serif;
  font-weight: 600;
  line-height: 27px;
  color: #2498fb;
  font-size: 22px;
  margin-bottom: 5.27px;
}

.serviceDetails p {
  color: #808080;
  line-height: 30px;
  font-size: 20px;
}
.serviceDetails h3 {
  line-height: 53px;
  color: #707070;
}
.serviceDetails h3,
.serviceDetails p {
  font-weight: 400;
}

.serviceDetails ul li:before {
  content: "\f00c";
  font-size: 1.1rem;
  font-family: fontAwesome;
  color: #535772;
  margin-right: 10px;
}
.card .cardContent ul li:before {
  content: "";
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-clip: content-box;
  padding: 2px;
  margin: 8px 10px 0 0;
  display: inline-block;

  border: 4px solid #0d76ba;
}
.testimonialCardOwner {
}
@media only screen and (max-width: 400px) {
  .wrapper,
  .card {
    min-width: 300px;
  }
  .serviceDetails p {
    font-size: 14px;
  }
  .serviceCardContent ul li {
    font-size: 16px;
    line-height: 22px;
  }
  .serviceTitle {
    font-size: 22px;
    line-height: 30px;
  }
  .cardHeader {
    font-size: 20px;
  }
  .serviceDetails ul li {
    font-size: 16px;
  }
}

@media only screen and (max-width: 897px) {
  .serviceContainer,
  .serviceCardContainer {
    flex-direction: column-reverse;
  }
  .serviceCardContent ul li {
    font-size: 18px;
    line-height: 24px;
  }
  .serviceDetails p {
    font-size: 16px;
  }
  .serviceTitle {
    font-size: 26px !important;
    line-height: 34px;
  }
  .serviceDetailContainer h1 {
    font-size: 30px;
  }
  .cardHeader {
    font-size: 22px;
  }
  .serviceDetailContainer {
    margin-bottom: 30px;
  }
  .serviceDetails h3 {
    font-size: 24px;
  }
  .serviceDetails {
    width: 90%;
  }
  .serviceDetails ul li {
    font-size: 18px;
  }
}

@media only screen and (min-width: 897px) and (max-width: 1199px) {
  .serviceContainer {
    flex-direction: column-reverse;
  }
  .serviceCardContainer {
    margin-top: 40px;
  }
  .serviceCardContent ul li {
    font-size: 20px;
  }
  .cardHeader {
    font-size: 24px;
  }
  .serviceDetails p {
    font-size: 18px;
    line-height: 30px;
  }
  .serviceTitle {
    font-size: 29px !important;
    line-height: 38px;
  }
  .serviceDetailContainer h1 {
    font-size: 35px;
  }
  .serviceDetails h3 {
    font-size: 26px;
  }
  .serviceDetails ul li {
    font-size: 20px;
  }
}

@media only screen and (min-width: 897px) {
  .serviceDetailContainer {
    align-items: flex-start;
    max-width: 80%;
  }
}
