.App {
  font-family: sans-serif;
  text-align: center;
}
.portfolioContainer {
  margin-bottom: 60px;
  /* background-color: #0099ff; */
}
.owl-item .item {
  min-height: 500px;
  display: flex;
  align-items: center;
}
.PortfolioheadingContainer .serviceDescription {
  margin-top: 10px;
  font-family: Poppins, sans-serif;
  line-height: 69px;
  font-size: 46px;
  font-weight: 600;
}
.serviceDescription-subheading {
  border: 2px dashed #707070;
  border-radius: 10px;
  padding: 17.5px 29px 17.5px 22px;
  line-height: 34px;
  width: min-content;
  color: #535772;
  font-family: Montserrat, sans-serif;
  font-size: 28px;
  font-weight: 600;
}

.PortfolioheadingContainer {
  margin-bottom: 50px;
}
.portfolio-carousel .owl-dots {
  display: none;
}
.portfolio-carousel .owl-stage-outer {
  /* overflow-y:visible; */
}

.portfolio-carousel .owl-stage-outer {
  position: relative;
}
.portfolio-carousel .owl-nav {
  background-color: #fff;
  font-size: 40px;
  position: absolute;
  border-radius: 0px 15px 15px 0px;
  box-shadow: 0px 5px 15px #00000029;
  min-width: 50px;
  min-height: 60px;
  left: 50%;
  bottom: 50%;
}
.portfolio-carousel .owl-nav:hover {
  background-color: #2498fb;
}
.portfolioButton {
  padding-top: 23px;
  padding-bottom: 22px;
  border-radius: 15px;
  height: 60px;
  font-size: 27px;
  font-weight: 500;
  font-family: Poppins, sans-serif;
  line-height: 40px;
}
.portfolio-carousel .owl-nav .owl-prev {
  display: none;
}
.carouselitem {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding-left: 40px;
  background-color: #0099ff;
  padding-right: 40px;
}
.carouselitem .carouselText .serviceDetailContainer {
  height: 110%;
  position: relative;
  top: -20px;
}
.carouselitem .carouselText .serviceDetailContainer .serviceDetails {
  height: 100%;
}
.carouselImage {
  width: 45%;
  align-self: center;
}
.carouselText {
  width: 45%;
}
.carouselText .serviceDetails {
  width: 90%;
}
.serviceDetails h3 {
  font-weight: 400;
  font-family: Poppins, sans-serif;
  color: #707070;
  line-height: 53px;
  font-size: 35px;
}
.serviceDetails h4 {
  font-family: Montserrat, sans-serif;
  font-weight: 600;
  color: #535772;
  line-height: 34px;
  font-size: 28px;
}
.serviceDetils p {
  color: #808080;
  line-height: 30px;
  font-size: 20px;
  font-weight: 400;
  font-family: Poppins, sans-serif;
}
.owl-theme .owl-nav [class*="owl-"]:hover {
  background-color: #2498fb;
}
.owlstageouter {
  overflow: visible;
}

@media only screen and (max-width: 400px) {
  .carouselImage {
    width: 100%;
    margin-bottom: 30px;
  }
  .PortfolioheadingContainer .serviceDescription {
    font-size: 30px;
    line-height: 40px;
  }
  .serviceDescription-subheading {
    font-size: 20px;
    padding: 10.5px 21px 10.5px 15px;
    line-height: 26px;
  }
  .serviceDetails h3 {
    font-size: 26px;
    line-height: 38px;
  }
  .serviceDetails h4 {
    font-size: 22px;
    line-height: 28px;
  }
  .portfolioButton {
    font-size: 20px;
    height: 45px;
  }
}

@media only screen and (max-width: 897px) {
  .portfolio-carousel .owl-nav {
    left: 90%;
  }
  .carouselitem {
    align-items: center;
    flex-direction: column;
    margin: auto;
  }
  .carouselImage {
    width: 80%;
    margin-bottom: 30px;
  }
  .carouselText {
    width: 80%;
  }
  .PortfolioheadingContainer .serviceDescription {
    font-size: 34px;
    line-height: 48px;
  }
  .serviceDescription-subheading {
    font-size: 22px;
    padding: 13.5px 22px 13.5px 17px;
    line-height: 28px;
  }
  .portfolioContainer {
    margin-bottom: 20px;
    padding-top: 20px;
  }
  .serviceDetails h3 {
    font-size: 31px;
    line-height: 44px;
  }
  .serviceDetails h4 {
    font-size: 24px;
    line-height: 30px;
  }
  .portfolioButton {
    padding-top: 16px;
    font-size: 22px;
    padding-bottom: 14px;
    height: 50px;
  }
}

