.testimonialContainer {
  padding-top: 50px;
  padding-bottom: 50px;
}
.testimonialTexts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.testimonialTitle {
  margin-bottom: 20px;
  font-weight: 600;
}
.feedbackTitle {
  font-weight: 300;
}
.testimonialTitle,
.feedbackTitle {
  text-align: left;
  margin-top: 0;
  line-height: 98px;
  font-size: 65px;
  letter-spacing: 0px;
  color: #535772;
  font-family: Poppins, sans-serif;
  position: relative;
  opacity: 1;
}
.testimonialTitle:after {
  content: "";
  position: absolute;
  height: 4px;
  max-width: 300px;
  width: 100%;
  left: 0;
  right: 0;

  bottom: -20px;
  background: #2498fb 0% 0% no-repeat padding-box;
  box-shadow: 0px 5px 15px #00000029;
  border-radius: 15px;
  opacity: 1;
}
.testimonialContainer .testimonialCard {
  position: relative;
  max-width: 420px;
  box-shadow: 0px 5px 10px #00000029;
  padding: 30px 30px 20px 30px;
  overflow: visible;
  border-radius: 15px;
}
.testimonialCard::before {
  content: "\f10d";
  position: absolute;
  color: #2498fb;
  font-size: 55px;
  font-weight: bold;
  top: -40px;
  font-family: fontAwesome;
}
.testimonialcardContent {
  font-family: Poppins, sans-serif;
  font-weight: medium;
  font-size: 26px;
  line-height: 39px;
  color: #808080;
  letter-spacing: 0px;
}
.testimonialCardOwner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}
.testimonialCardOwner h4,
.testimonialCardOwner h6 {
  font-family: Poppins, sans-serif;
  text-align: center;
  letter-spacing: 0px;
  margin: 0;
}
.testimonialCardOwner h4 {
  font-size: 28px;
  font-weight: 500;
  line-height: 42px;
  color: #0d76ba;
}
.testimonialCardOwner h6 {
  font-weight: 400;
  line-height: 30px;

  font-size: 20px;
  color: #808080;
  opacity: 1;
}

.feedbackTitle {
  margin-top: 10px;
}
@media only screen and (max-width: 400px) {
  .testimonialTitle,
  .feedbackTitle {
    font-size: 32px;
    line-height: 40px;
  }
  .feedbackTitle {
    margin-top: 5px;
  }
}
@media only screen and (max-width: 600px) {
  .testimonialContainer {
    margin-bottom: 56px;
  }
  .testimonialContainer .testimonialCard {
    margin-top: 30px;
  }
}

@media only screen and (max-width: 897px) {
  .testimonialTitle,
  .feedbackTitle {
    font-size: 40px;
    line-height: 50px;
  }
  .testimonialCardOwner h4 {
    font-size: 25px;
  }
  .testimonialTitle:after {
    height: 3px;
    bottom: -10px;
  }
  .testimonialCardOwner h6 {
    font-size: 18px;
  }
  .testimonialTexts {
    align-items: center;
    margin-bottom: 20px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1330px) {
  .testimonialTitle {
    font-size: 48px;
    line-height: 80px;
  }
  .testimonialTexts {
    margin-bottom: 30px;
  }
  .serviceTitle {
    margin-bottom: 20px;
  }
}
